import { useReactiveVar } from "@apollo/client";
import { useEffect } from "react";
import { Var_consultingInputData } from "./Lib_consultingForm";

export default function App() {
  const consultingInputData = useReactiveVar(Var_consultingInputData);
  useEffect(() => {
    const newConsultingInputData = JSON.parse(
      window.localStorage.getItem("consultingInputData") ||
        JSON.stringify(consultingInputData)
    );
    Var_consultingInputData(newConsultingInputData);
  }, []);

  return <></>;
}
