import { useReactiveVar } from "@apollo/client";
import { useEffect, useRef, useState } from "react";
import { isMobile } from "react-device-detect";
import Modal from "react-modal";
import styled from "styled-components";
import Hook_scrollLock from "../Payment1_1/Hook_scrollLock";
import { visualPaymentDataVar } from "./Var_priceData";

const data1 = {
  button: <span>포케팅 서비스 이용약관 동의</span>,
  modal: <div>내용물</div>,
};

export default function App({ data = data1 }) {
  Modal.setAppElement("#root");
  const [modalIsOpen, setIsOpen] = useState(false);
  const customStyles = isMobile
    ? {
        content: {
          top: "auto",
          left: "50%",
          right: "auto",
          marginRight: "-50%",
          transform: "translate(-50%)",
          padding: 0,
          borderRadius: "0.5rem",
          borderBottomLeftRadius: "0",
          borderBottomRightRadius: "0",
          bottom: "-2px",
          zindex: 1000,
        },
      }
    : {
        content: {
          top: "51%",
          left: "50%",
          right: "auto",
          bottom: "auto",
          marginRight: "-50%",
          transform: "translate(-50%, -50%)",
          padding: 0,
          borderRadius: "0.5rem",
          zindex: 1000,
        },
      };

  function openModal() {
    setIsOpen(true);
  }

  function afterOpenModal() {}

  function closeModal() {
    setIsOpen(false);
  }

  const element1: any = useRef();
  const visualPaymentData = useReactiveVar(visualPaymentDataVar);
  const indexOfVisualPaymentData = visualPaymentData
    .map((val, idx) => val.clicked)
    .indexOf(true);
  const scrollToY =
    indexOfVisualPaymentData === 0
      ? 0
      : indexOfVisualPaymentData === 1
      ? 330
      : 1000;

  return isMobile ? (
    <div className="relative z-40 ">
      <span
        onClick={async () => {
          await openModal();
          await element1.current.scrollTo({ top: scrollToY });
        }}
        className="z-30"
      >
        {data.button}
      </span>
      <Modal
        isOpen={modalIsOpen}
        onAfterOpen={afterOpenModal}
        onRequestClose={closeModal}
        style={customStyles}
        contentLabel="Modal2"
      >
        <div className="flex justify-end mr-4 mt-2 cursor-pointer">
          <div onClick={closeModal} className=" cursor-pointer">
            <i className="far fa-times-circle text-xl text-gray-400"></i>
          </div>
        </div>
        <div
          ref={element1}
          className="overflow-y-scroll "
          style={{ height: "80vh", width: "98vw" }}
        >
          <div>
            <Hook_scrollLock />
            {data.modal}
          </div>
        </div>
      </Modal>
    </div>
  ) : (
    <div className="relative z-40 ">
      <span onClick={openModal} className="z-30">
        {data.button}
      </span>
      <Modal
        isOpen={modalIsOpen}
        onAfterOpen={afterOpenModal}
        onRequestClose={closeModal}
        style={customStyles}
        contentLabel="Modal2"
      >
        <div className="flex justify-end mr-4 mt-2">
          <div onClick={closeModal} className=" cursor-pointer">
            <i className="far fa-times-circle text-xl text-gray-400"></i>
          </div>
        </div>
        <div className=" " style={{ width: "65rem" }}>
          <div className="p-4">
            <Hook_scrollLock />
            {data.modal}
          </div>
        </div>
      </Modal>
    </div>
  );
}
