import { useReactiveVar } from "@apollo/client";
import { useState } from "react";
import { isMobile } from "react-device-detect";
import useCheckbox from "../../../hooks/useCheckbox";
import { modalData1 } from "./const_modalData";
import { Input_bigConsulting, Input_consulting } from "./Input_consulting";
import {
  consultingInpuValidate,
  formVarOnchange,
  Var_agreeCheck,
  Var_consultingInpuSetting,
  Var_consultingInputData,
} from "./Lib_consultingForm";
import Mol_payButton from "./Button_question";
import Tem_modal1 from "./Tem_modal1";
const data1 = {
  title: (
    <>
      무료로 전문가의 컨설팅을 {isMobile ? <br /> : " "}
      받아보세요.
    </>
  ),
  subtitle: <>24시간 내로 연락드립니다</>,
};

export default function App() {
  const inputData = useReactiveVar(Var_consultingInputData);
  const inputSetting = useReactiveVar(Var_consultingInpuSetting);
  const layout = isMobile ? "w-11/12" : " w-full";
  const [depositClickedState, setDepositClickedState] = useState(false);

  const agreeCheckbox1 = useCheckbox(false);
  const agreeCheckbox2 = useReactiveVar(Var_agreeCheck);

  const modalTemplete = (data) => ({
    button: <span className="underline cursor-pointer">{data.title}</span>,
    modal: (
      <div className="p-3">
        <div className=" text-xl font-bold mb-4">{data.title}</div>
        <ul>
          {data.contents.map((val, idx) => (
            <li key={idx}>
              <div className="font-bold mb-2">{val.title}</div>
              <div className="mb-6">{val.p}</div>
            </li>
          ))}
        </ul>
      </div>
    ),
  });

  return isMobile ? (
    <section className="">
      <div className="flex flex-col items-center text-center py-10">
        <div className=" font-bold text-xl mb-4">{data1.title}</div>
        <div className=" font-bold">{data1.subtitle}</div>
      </div>

      <ul className={`${layout} mx-auto mb-1`}>
        {inputSetting.map((val, idx) =>
          idx !== 4 ? (
            <li className="mb-4 ">
              <div className="text-lg mb-1">
                {val.title}
                {idx !== 6 ? (
                  <span className="text-orange-500 ml-1">*</span>
                ) : (
                  <></>
                )}
              </div>
              <div className="h-0 relative z-30 flex justify-end">
                <div className=" h-10 w-max flex items-center mr-2">
                  {consultingInpuValidate[idx](
                    inputData[idx] ? inputData[idx] : ""
                  ) && idx !== 6 ? (
                    <>
                      <i className="fas fa-check-circle text-lg"></i>
                    </>
                  ) : (
                    <></>
                  )}
                </div>
              </div>
              {idx !== 6 ? (
                <Input_consulting
                  value={inputData[idx]}
                  onChange={(e) => {
                    formVarOnchange(e, inputData, idx);
                  }}
                  placeholder={val.inputProps?.placehoder}
                  type={val.inputProps?.type}
                  name={val.inputProps?.name}
                />
              ) : (
                <Input_bigConsulting
                  value={inputData[idx]}
                  onChange={(e) => {
                    formVarOnchange(e, inputData, idx);
                  }}
                  placeholder={val.inputProps?.placehoder}
                  name={val.inputProps?.name}
                />
              )}
            </li>
          ) : (
            // 예산 부분
            <>
              <li className="mb-4 relative z-50">
                <div className="text-lg mb-1">
                  {val.title}
                  <span className="text-orange-500 ml-1">*</span>
                </div>
                <div
                  className=" bg-gray-100 px-4 py-2 text-gray-500 rounded border-2 flex justify-between  cursor-pointer"
                  onClick={() => {
                    setDepositClickedState(!depositClickedState);
                  }}
                >
                  <div className="">{inputData[idx]}</div>
                  <div className="">
                    {depositClickedState ? (
                      <i className="fas fa-chevron-up"></i>
                    ) : (
                      <i className="fas fa-chevron-down"></i>
                    )}
                  </div>
                </div>
                {depositClickedState ? (
                  <div className="h-0">
                    <ul className="bg-white border border-gray-200 rounded-md p-4 pb-0 shadow-lg">
                      {val.selectList?.map((val, idx) => (
                        <li
                          className="pb-2 mb-2 border-b-2 last:border-0  text-gray-700  cursor-pointer"
                          onClick={() => {
                            Var_consultingInputData(
                              inputData.map((val1, idx1) =>
                                idx1 === 4 ? val : val1
                              )
                            );
                            setDepositClickedState(false);
                          }}
                        >
                          {val}
                        </li>
                      ))}
                    </ul>
                  </div>
                ) : (
                  <></>
                )}
              </li>
            </>
          )
        )}
      </ul>

      <div className="pb-6 mb-8 border-b-2 "></div>
      <div className="w-11/12 mx-auto">
        {/* 체크박스1 */}
        <div
          {...agreeCheckbox1}
          className=" flex items-center mb-2 cursor-pointer"
        >
          <span className="mr-2 text-xl text-orange-500 ">
            {agreeCheckbox1.value ? (
              <i className="far fa-check-square"></i>
            ) : (
              <i className="far fa-square"></i>
            )}
          </span>

          <span className="text-gray-500">
            <Tem_modal1 data={modalTemplete(modalData1)}></Tem_modal1>
          </span>

          <span className="text-gray-500">에 동의합니다. (필수)</span>
        </div>

        {/* 체크박스2 */}
        <div
          onClick={() => {
            Var_agreeCheck(!agreeCheckbox2);
          }}
          className=" flex items-center mb-4 cursor-pointer"
        >
          <span className="mr-2 text-xl text-orange-500 ">
            {agreeCheckbox2 ? (
              <i className="far fa-check-square"></i>
            ) : (
              <i className="far fa-square"></i>
            )}
          </span>
          <span className="text-gray-500">대행사라면 체크해주세요. (선택)</span>
        </div>

        <div className="mb-10"></div>

        <div className="flex justify-center">
          <Mol_payButton clickable={agreeCheckbox1.value}></Mol_payButton>
        </div>

        <div className="mb-16"></div>
      </div>
    </section>
  ) : (
    // 피씨
    // 피씨
    // 피씨
    // 피씨
    // 피씨
    // 피씨
    <section className="max-w-screen-lg mx-auto py-14">
      <div className="flex flex-col items-center text-center py-10">
        <div className=" font-bold text-3xl mb-4">{data1.title}</div>
        <div className=" font-bold text-lg">{data1.subtitle}</div>
      </div>

      <ul className={` mx-auto mb-1 grid grid-cols-2`}>
        {inputSetting.map((val, idx) =>
          idx < 6 ? (
            idx !== 4 ? (
              <li key={idx} className="mb-4 px-5">
                <div className="text-lg mb-1">
                  {val.title}
                  {idx !== 6 ? (
                    <span className="text-orange-500 ml-1">*</span>
                  ) : (
                    <></>
                  )}
                </div>
                <div className="h-0 relative z-30 flex justify-end">
                  <div className=" h-10 w-max flex items-center mr-2">
                    {consultingInpuValidate[idx](
                      inputData[idx] ? inputData[idx] : ""
                    ) && idx !== 6 ? (
                      <>
                        <i className="fas fa-check-circle text-lg"></i>
                      </>
                    ) : (
                      <></>
                    )}
                  </div>
                </div>
                {idx !== 6 ? (
                  <Input_consulting
                    value={inputData[idx]}
                    onChange={(e) => {
                      formVarOnchange(e, inputData, idx);
                    }}
                    placeholder={val.inputProps?.placehoder}
                    type={val.inputProps?.type}
                    name={val.inputProps?.name}
                  />
                ) : (
                  <Input_bigConsulting
                    value={inputData[idx]}
                    onChange={(e) => {
                      formVarOnchange(e, inputData, idx);
                    }}
                    placeholder={val.inputProps?.placehoder}
                    name={val.inputProps?.name}
                  />
                )}
              </li>
            ) : (
              // 예산 부분
              <>
                <li className="mb-4 relative z-50 px-5">
                  <div className="text-lg mb-1">
                    {val.title}
                    <span className="text-orange-500 ml-1">*</span>
                  </div>
                  <div
                    className=" bg-gray-100 px-4 py-2 text-gray-500 rounded border-2 flex justify-between  cursor-pointer"
                    onClick={() => {
                      setDepositClickedState(!depositClickedState);
                    }}
                  >
                    <div className="">{inputData[idx]}</div>
                    <div className="">
                      {depositClickedState ? (
                        <i className="fas fa-chevron-up"></i>
                      ) : (
                        <i className="fas fa-chevron-down"></i>
                      )}
                    </div>
                  </div>
                  {depositClickedState ? (
                    <div className="h-0">
                      <ul className="bg-white border border-gray-200 rounded-md p-4 pb-0 shadow-lg">
                        {val.selectList?.map((val, idx) => (
                          <li
                            className="pb-2 mb-2 border-b-2 last:border-0  text-gray-700 cursor-pointer"
                            onClick={() => {
                              Var_consultingInputData(
                                inputData.map((val1, idx1) =>
                                  idx1 === 4 ? val : val1
                                )
                              );
                              setDepositClickedState(false);
                            }}
                          >
                            {val}
                          </li>
                        ))}
                      </ul>
                    </div>
                  ) : (
                    <></>
                  )}
                </li>
              </>
            )
          ) : (
            <></>
          )
        )}
      </ul>

      <div className="mt-6 mx-5 mb-10">
        <div className="text-lg mb-1">{inputSetting[6].title}</div>
        <Input_bigConsulting
          value={inputData[6]}
          onChange={(e) => {
            formVarOnchange(e, inputData, 6);
          }}
          placeholder={inputSetting[6].inputProps?.placehoder}
          name={inputSetting[6].inputProps?.name}
        />
      </div>

      <div className="">
        <div className="flex justify-center">
          {/* 체크박스1 */}
          <div
            {...agreeCheckbox1}
            className=" flex items-center cursor-pointer mr-10"
          >
            <span className="mr-2 text-xl text-orange-500 ">
              {agreeCheckbox1.value ? (
                <i className="far fa-check-square"></i>
              ) : (
                <i className="far fa-square"></i>
              )}
            </span>

            <span className="text-gray-500">
              <Tem_modal1 data={modalTemplete(modalData1)}></Tem_modal1>
            </span>

            <span className="text-gray-500">에 동의합니다. (필수)</span>
          </div>

          {/* 체크박스2 */}
          <div
            onClick={() => {
              Var_agreeCheck(!agreeCheckbox2);
            }}
            className=" flex items-center cursor-pointer"
          >
            <span className="mr-2 text-xl text-orange-500 ">
              {agreeCheckbox2 ? (
                <i className="far fa-check-square"></i>
              ) : (
                <i className="far fa-square"></i>
              )}
            </span>
            <span className="text-gray-500">
              대행사라면 체크해주세요. (선택)
            </span>
          </div>
        </div>

        <div className="pb-10 mb-12 border-b-2"></div>

        <div className="flex justify-center">
          <Mol_payButton clickable={agreeCheckbox1.value}></Mol_payButton>
        </div>

        <div className="mb-10"></div>
      </div>
    </section>
  );
}
