import { gql, useMutation, useReactiveVar } from "@apollo/client";
import { isMobile } from "react-device-detect";
import { useHistory } from "react-router";
import {
  consultingInpuValidate,
  Var_agreeCheck,
  Var_consultingInpuSetting,
  Var_consultingInputData,
} from "../Consulting1_1/Lib_consultingForm";

export default function App({ setEditState }) {
  const agreeCheckbox2 = useReactiveVar(Var_agreeCheck);
  // 메일발송
  const consultingInputData = useReactiveVar(Var_consultingInputData);

  // 액션팀에게
  const SENDMAIL_TO_ACTIONTEAM = gql`
    mutation {
      sendQuestionEditMailToAction(
        from: "poketing_mail_server@poketing.com"
        to: "sp_team@poketing.com"
        subject: "[공홈] 수정 된 컨설팅 상담 정보 전달"
        brandName:"${consultingInputData[0]}"
        name:"${consultingInputData[1]}"
        phoneNumber:"${consultingInputData[2]}"
        email:"${consultingInputData[3]}"
        budget:"${consultingInputData[4]}"
        link:"${consultingInputData[5]}"
        etc:"${consultingInputData[6]}"
      )
    }
  `;
  const [sendMailMutation1] = useMutation(SENDMAIL_TO_ACTIONTEAM);

  // 클라이언트에게
  const SENDMAIL_TO_CLIENT = gql`
    mutation {
      sendQuestionEditMailToClient(
        from: "sp_team@poketing.com"
        to: "${consultingInputData[3]}"
        subject: "[포케팅] 수정하신 컨설팅 상담 정보 확인 부탁 드립니다."
        brandName:"${consultingInputData[0]}"
        name:"${consultingInputData[1]}"
        phoneNumber:"${consultingInputData[2]}"
        email:"${consultingInputData[3]}"
        budget:"${consultingInputData[4]}"
        link:"${consultingInputData[5]}"
        etc:"${consultingInputData[6]}"

      )
    }
  `;
  const [sendMailMutation2] = useMutation(SENDMAIL_TO_CLIENT);

  const history = useHistory();
  // 폼체크하고 발송
  const formCheck = () => {
    consultingInpuValidate.map((val, idx) => {
      if (!val(consultingInputData[idx])) {
        const error = Var_consultingInpuSetting()[idx].validateError;
        throw error;
      }
    });
    sendMailMutation1();
    sendMailMutation2();
    window.localStorage.setItem(
      "consultingInputData",
      JSON.stringify(consultingInputData)
    );
    setEditState(false);
  };

  return (
    <div
      className="w-max"
      onClick={() => {
        try {
          formCheck();
        } catch (e) {
          alert(e);
        }
      }}
    >
      <div className="flex px-10 py-1 rounded-lg cursor-pointer  w-max text-lg border-2 text-gray-600">
        수정완료
      </div>
    </div>
  );
}
