const App = () => {
  const [Trigger1, setTrigger1] = useState(false);
  const [Trigger2, setTrigger2] = useState(false);
  const [Trigger3, setTrigger3] = useState(false);
  const [aosTrigger1, setaosTrigger1] = useState(false);
  const element1: any = useRef();
  const element2: any = useRef();
  const element3: any = useRef();
  const aoselement1: any = useRef();
  const scrollYCheck = () => {
    const elementCoordinate1 = element1.current
      ? element1.current.getBoundingClientRect().bottom
      : 0;
    const elementCoordinate2 = element2.current
      ? element2.current.getBoundingClientRect().top
      : 0;
    const elementCoordinate3 = element3.current
      ? element3.current.getBoundingClientRect().top
      : 0;
    const aosElementCoordinate1 = aoselement1.current
      ? aoselement1.current.getBoundingClientRect().top
      : 0;
    if (elementCoordinate1 < window.innerHeight - 10) {
      setTrigger1(true);
    } else {
      setTrigger1(false);
    }
    if (elementCoordinate2 < -300 && elementCoordinate3 > 300) {
      setTrigger2(true);
    } else {
      setTrigger2(false);
    }
    if (elementCoordinate3 < 300) {
      setTrigger3(true);
    } else {
      setTrigger3(false);
    }
    if (aosElementCoordinate1 < window.innerHeight - 500) {
      setaosTrigger1(true);
    } else {
      setaosTrigger1(false);
    }
  };
  const data9 = {
    Trigger1,
    Trigger2,
    Trigger3,
    aosTrigger1,
    p1: (
      <>
        우리 브랜드도 <span className="font-bold">고객 후기영상</span>으로{" "}
        <span className="font-bold">성과</span> 낼 수 <br />
        있는지 바로 컨설팅 받아 보세요.
      </>
    ),
    p2: (
      <>
        <span className="font-bold">월 매출 1억</span>, 우리도 가능할까요?
        고민하지 <br />
        말고 <span className="font-bold">무료로 컨설팅</span>받아보세요.
      </>
    ),
  };
  const throttleScrollYCheck = throttle(200, scrollYCheck);
  useEffect(() => {
    window.addEventListener("scroll", throttleScrollYCheck);
    return () => window.removeEventListener("scroll", throttleScrollYCheck);
  }, []);

  const pushData1 = PushDataLayer(
    "clickClasses",
    "more middle service-visual pc",
    "More"
  );

  return (
    <>
      <Org_floatingButton />
      <SuccessTop1 data={data1}></SuccessTop1>
      <div ref={element1}>
        <SuccessMid1 data={data2}></SuccessMid1>
      </div>
      <div ref={element2}>
        <SuccessMid1 data={data4}></SuccessMid1>
      </div>

      {/* 자세히 보기 링크 */}
      <div className="max-w-screen-lg mx-auto text-center mb-28">
        <Link
          to="/service/visual-influencer/introduce"
          onClick={() => pushData1()}
        >
          <span>📌</span>
          <span className="font-bold text-lg text-blue-500 underline">
            고객에게 7배 더 반응이 좋은 마케팅 자세히 보기
          </span>
        </Link>
      </div>

      <div ref={aoselement1}>
        <SuccessMid1 data={data5}>
          <div className="flex justify-center" style={{ width: "1024px" }}>
            <div className="flex">
              <div
                className={` relative left-10 ${
                  aosTrigger1 ? "top-0 opacity-100" : " top-10 opacity-0"
                }`}
                style={{ transition: "ease 0.5s" }}
              >
                <img
                  src={
                    require("/home/po-main1-front/src/assets/고객사례 운동기구/3_1.png")
                      .default
                  }
                ></img>
              </div>
              <div
                className={` relative ${
                  aosTrigger1 ? "right-14 opacity-100" : " right-24 opacity-0"
                }`}
                style={
                  aosTrigger1
                    ? { transition: "ease 0.5s 0.4s" }
                    : { transition: "ease 0.2s" }
                }
              >
                <img
                  src={
                    require("/home/po-main1-front/src/assets/고객사례 운동기구/3_2.png")
                      .default
                  }
                ></img>
              </div>
            </div>
          </div>
        </SuccessMid1>
      </div>
      <div ref={element3}>
        <SuccessMid1 data={data5_2}></SuccessMid1>
      </div>
      <SuccessMent1 data={data6}></SuccessMent1>
      <div className="max-w-screen-lg mx-auto">
        <H1>비주얼 인플루언서 콘텐츠 레퍼런스</H1>
        <div className="mt-6">
          <CategoriBox>운동기구</CategoriBox>
        </div>
      </div>
      <SuccessCarousel1 data={data7}></SuccessCarousel1>

      {/* 고객사례 더보기 */}
      <Org_costomerReact />

      <SuccessBot1 data={data8}></SuccessBot1>
    </>
  );
};

