import React, { useState } from "react";
import Modal from "/home/po-main1-front/src/hooks/useModal";

const App = ({ data, children }: any) => {
  const [modalVisible, setModalVisible] = useState(false);
  const openModal = () => {
    setModalVisible(true);
  };
  const closeModal = () => {
    setModalVisible(false);
  };
  return (
    <>
      <div>
        {modalVisible && (
          <Modal
            visible={modalVisible}
            closable={true}
            maskClosable={true}
            onClose={closeModal}
            width="92vw"
          >
            {children}
          </Modal>
        )}
        <div onClick={openModal}>{data.title}</div>
      </div>
    </>
  );
};

export default App;
