import { useState } from "react";

export default function useCheckbox(initialValue: any) {
  const [value, setValue] = useState(initialValue);
  const checkToggle = () => {
    value ? setValue(false) : setValue(true);
  };
  const onClick = () => {
    checkToggle();
  };
  return { value, onClick };
}

