import { useReactiveVar } from "@apollo/client";
import { useState } from "react";
import { isMobile } from "react-device-detect";
import Button_goToPayment from "./Button_goToPayment";
import { visualPaymentDataVar } from "./Var_priceData";

export default function App() {
  const click1Toggle = (id) => {
    const newVisualPaymentData = [...visualPaymentDataVar()];

    const setClickState = newVisualPaymentData.map((val, idx) =>
      idx === id ? { ...val, clicked: true } : { ...val, clicked: false }
    );
    visualPaymentDataVar(setClickState);
  };

  const data1 = useReactiveVar(visualPaymentDataVar);
  return isMobile ? (
    // 모바일
    // 모바일
    // 모바일
    // 모바일
    // 모바일
    // 모바일
    <section>
      <div className="py-20 bg-gray-50">
        <div className="text-2xl font-bold text-center mb-10">
          성장 로켓에 <br />
          올라타고 성과를 높이세요.
        </div>
        <ul className="w-11/12 mx-auto">
          {data1.map((val, idx) => (
            <li
              key={idx}
              className={`my-4 ring-2 rounded-md bg-white  transition duration-200 ${
                val.clicked ? "ring-indigo-400 " : "ring-gray-400 opacity-40"
              }`}
              onClick={() => {
                click1Toggle(idx);
              }}
            >
              <div className=" p-4 py-6 ">
                <div className=" text-orange-500 text-sm font-bold mb-1">
                  {val.highlightedText}
                </div>
                <div className="font-bold text-lg mb-5">{val.title}</div>
                <div className="mb-6">
                  {val.mainText.map((val, idx) => (
                    <div className=" mb-1">
                      ✅ {val.title} {val.account}
                    </div>
                  ))}
                </div>
                <div className="flex  justify-between  text-gray-600">
                  <div className="flex items-end">총 서비스 금액</div>
                  <div className="flex">
                    <div className="font-bold">{val.totalPrice}</div>
                  </div>
                </div>
              </div>
              <div
                className="bg-gradient-to-r from-indigo-400 to-indigo-300 rounded-b-md text-white px-4 py-3 flex justify-between text-lg text-shadow-sm"
                style={{ zIndex: -1 }}
              >
                <div>6개월 할부시</div>
                <div>{val.installmentPrice}</div>
              </div>
            </li>
          ))}
        </ul>
      </div>
    </section>
  ) : (
    // 피씨
    // 피씨
    // 피씨
    // 피씨
    // 피씨
    // 피씨
    <section>
      <div className="py-20 bg-gray-50">
        <div className="text-3xl font-bold text-center mb-14  leading-normal">
          성장 로켓에 <br />
          올라타고 성과를 높이세요.
        </div>
        <ul className=" max-w-screen-lg mx-auto grid grid-cols-3 gap-6">
          {data1.map((val, idx) => (
            <li
              key={idx}
              className={`my-4 ring-2  rounded-md bg-white  transition duration-200 cursor-pointer flex flex-col justify-end ${
                val.clicked ? "ring-indigo-400" : "ring-gray-400 opacity-40"
              }`}
              onClick={() => {
                click1Toggle(idx);
              }}
            >
              <div className=" p-4 py-6 ">
                <div className=" text-orange-500 text-sm font-bold mb-1">
                  {val.highlightedText}
                </div>
                <div className="font-bold text-lg mb-5">{val.title}</div>
                <div className="mb-6">
                  {val.mainText.map((val, idx) => (
                    <div className=" mb-1">
                      ✅ {val.title} {val.account}
                    </div>
                  ))}
                </div>
                <div className="flex  justify-between  text-gray-600  h-16 items-end ">
                  <div className="flex items-end">총 서비스 금액</div>
                  <div className="flex">
                    <div className="font-bold">{val.totalPrice}</div>
                  </div>
                </div>
              </div>
              <div
                className="bg-gradient-to-r from-indigo-400 to-indigo-300 rounded-b-md text-white px-4 py-3 flex justify-between text-lg text-shadow-sm"
                style={{ zIndex: 0 }}
              >
                <div>6개월 할부시</div>
                <div>{val.installmentPrice}</div>
              </div>
            </li>
          ))}
        </ul>
        <div className=" flex justify-center mt-6 mb-2">
          <Button_goToPayment></Button_goToPayment>
        </div>
      </div>
    </section>
  );
}
