function reducer(state: any, action: any) {
  switch (action.type) {
    case "TITLE_SECLECT":
      const titleSelected = state;
      titleSelected.map((stateVal: any) => (stateVal.selected = false));
      titleSelected[action.dataIdx].selected = true;
      return [...titleSelected];

    case "PLAN_SECLECT":
      const planSelected = state;
      planSelected[action.dataIdx].plan.map(
        (planVal: any) => (planVal.selected = false)
      );
      planSelected[action.dataIdx].plan[action.planIdx].selected = true;
      return [...planSelected];

    case "QUANTITY_SECLECT":
      const quantitySelected = state;
      quantitySelected[action.dataIdx].plan[action.planIdx].quantity =
        quantitySelected[action.dataIdx].plan[action.planIdx].quantityList[
          action.quantityIdx
        ];
      return [...quantitySelected];

    case "QUANTITYBOX_OPEN":
      const quantityBoxState1 = state;
      quantityBoxState1.map((stateVal: any) =>
        stateVal.plan.map((planVal: any) => (planVal.quantityBoxOpened = false))
      );
      quantityBoxState1[action.dataIdx].plan[action.planIdx].quantityBoxOpened =
        true;
      return [...quantityBoxState1];

    case "QUANTITYBOX_CLOSE":
      const quantityBoxState2 = state;
      quantityBoxState2.map((stateVal: any) =>
        stateVal.plan.map((planVal: any) => (planVal.quantityBoxOpened = false))
      );
      return [...quantityBoxState2];

    default:
      return state;
  }
}

// 카트 담기, 비우기
function addItems(args: any) {
  const cartItems = { ...cartItemsVar() };
  cartItems.listSelected = [
    ...cartItemsVar().listSelected,
    {
      h1: args.h1,
      h2: args.h2,
      plan: args.plan,
      quantity: args.quantity,
      quantityText: args.quantityText,
      price: args.price,
      multipliedPrice: args.price * args.quantity,
    },
  ];
  cartItems.totalCost = cartItems.listSelected.reduce(
    (acc: any, cur: any) => acc + cur.multipliedPrice,
    0
  );
  cartItemsVar(cartItems);
  window.localStorage.setItem("cartItems", JSON.stringify(cartItems));
  return;
}
function removeItems(args: any) {
  const cartItems = { ...cartItemsVar() };
  cartItems.listSelected = cartItemsVar().listSelected.filter(
    (e: any, Idx: number) => {
      return Idx !== args.Idx;
    }
  );
  cartItems.totalCost = cartItems.listSelected.reduce(
    (acc: any, cur: any) => acc + cur.multipliedPrice,
    0
  );
  cartItemsVar(cartItems);
  window.localStorage.setItem("cartItems", JSON.stringify(cartItems));
  return;
}

