import React from "react";
import styled from "styled-components";
import Slider from "react-slick";

const CustomCarousel = styled.div`
  width: 100%;
  & .slick-slider {
    height: 100%;
    & .slick-prev {
      z-index: 10;
      left: -1rem;
      padding-bottom: 1rem;
      &:before {
        font-size: 0rem;
        font-family: "Font Awesome 5 Free";
        font-weight: 900;
        color: grey;
        content: "\f053";
      }
    }
    & .slick-next {
      z-index: 10;
      right: -1rem;
      padding-bottom: 1rem;
      &:before {
        font-size: 0rem;
        font-family: "Font Awesome 5 Free";
        font-weight: 900;
        color: grey;
        content: "\f054";
      }
    }
    & .slick-dots {
      z-index: 10;
      bottom: -2.4rem;
      & button {
        &:before {
          color: #ea580c;
        }
      }
    }

    & div:focus {
      outline: none;
    }
  }
`;

const App = ({ data, children }: any) => {
  const settings = {
    dots: false,
    infinite: true,
    slidesToShow: 3,
    slidesToScroll: 1,
    autoplay: true,
    speed: 2000,
    autoplaySpeed: 4000,
    draggable: false,
    pauseOnHover: false,
  };
  return (
    <section className="max-w-screen-lg mx-auto pb-16 mb-24">
      <h1 className="text-3xl font-bold mb-14">{data.h1}</h1>
      <CustomCarousel style={data ? { height: data.height } : {}}>
        <Slider {...settings}>
          {data.src.map((srcVal: any) => (
            <div>
              <div className="flex justify-center items-center px-7">
                <video
                  loop
                  muted
                  autoPlay
                  playsInline
                  className="w-full h-full object-cover"
                  style={{
                    height: data.height,
                  }}
                >
                  <source src={srcVal.webm} type="video/webm" />
                  <source src={srcVal.mp4} type="video/mp4" />
                </video>
              </div>
            </div>
          ))}
        </Slider>
      </CustomCarousel>
    </section>
  );
};

export default App;
