import { isMobile } from "react-device-detect";
import { Link, useHistory } from "react-router-dom";
import MoBr from "../../../1atoms/MoBr";

const defaultData = {
  title: (
    <>
      B사가 진행한
      <MoBr />
      비주얼 인플루언서
      <MoBr />
      마케팅에 대해 <MoBr />더 알아보세요!
    </>
  ),
  btn: <>서비스 자세히 보기!</>,
  linkTo: "/service",
};

export default function App({ data = defaultData, trigger = false }) {
  const history = useHistory();
  return (
    <section className=" bg-gray-900">
      {isMobile ? (
        <div className=" w-10/12 mx-auto py-20">
          <div className=" text-2xl font-bold text-white mb-10">
            {data.title}
          </div>
          <div
            className={` border-4 border-orange-500 rounded font-bold text-lg text-center p-2 transition duration-200 ${
              trigger
                ? " text-white text-shadow-md bg-orange-500"
                : "text-orange-500 "
            }`}
            onClick={() => {
              history.push(data.linkTo);
            }}
          >
            {data.btn}
          </div>
        </div>
      ) : (
        <div className=""></div>
      )}
    </section>
  );
}
