import React, { useEffect, useRef, useState } from "react";
import { Link } from "react-router-dom";
import { throttle } from "throttle-debounce";
import { PushDataLayer } from "../../../../hooks/PushDataLayer";
import Carousel from "./Carousel";

function App({ data }: any) {
  const [blackMode, setBlackMode] = useState(false);
  const element: any = useRef();
  const scrollYCheck = () => {
    const { current } = element;
    const elementCoordinate = current ? current.getBoundingClientRect().top : 0;
    if (elementCoordinate + 400 < window.innerHeight) {
      setBlackMode(true);
    } else {
      setBlackMode(false);
    }
  };
  const throttleScrollYCheck = throttle(200, scrollYCheck);
  useEffect(() => {
    window.addEventListener("scroll", throttleScrollYCheck);
    return () => window.removeEventListener("scroll", throttleScrollYCheck);
  }, []);

  const pushData1 = PushDataLayer(
    data.btn && data.btn[0].pushData.name,
    data.btn && data.btn[0].pushData.data,
    data.btn && data.btn[0].pushData.eventName
  );
  return (
    <>
      <section
        className={`my-20 py-20 transition duration-1000 ${
          blackMode ? "bg-gray-800" : "bg-white"
        }`}
      >
        <div ref={element}>
          <Carousel data={data} blackMode={blackMode}></Carousel>
        </div>
        {data.btn &&
          data.btn.map((btnVal: any) => (
            <>
              <div className="flex justify-center">
                <Link
                  to="/costomer-story/visual-influencer"
                  className=" bg-orange-500 rounded-lg text-white text-lg font-bold text-shadow-md mt-16 w-44 p-3 px-4 flex justify-center cursor-pointer shadow-lg"
                  onClick={() => pushData1()}
                >
                  {btnVal.text}
                </Link>
              </div>
            </>
          ))}
      </section>
    </>
  );
}

export default App;
