import React, { useEffect, useRef, useState } from "react";
import { Link } from "react-router-dom";
import { throttle } from "throttle-debounce";
import { PushDataLayer } from "/home/po-main1-front/src/hooks/PushDataLayer";

const App = ({ data, children }: any) => {
  const [Trigger, setTrigger] = useState(false);
  const element: any = useRef();
  const scrollYCheck = () => {
    const { current } = element;
    const elementCoordinate = current ? current.getBoundingClientRect().top : 0;
    if (elementCoordinate < window.innerHeight - 500) {
      setTrigger(true);
    } else {
      setTrigger(false);
    }
  };
  const throttleScrollYCheck = throttle(200, scrollYCheck);
  useEffect(() => {
    window.addEventListener("scroll", throttleScrollYCheck);
    return () => window.removeEventListener("scroll", throttleScrollYCheck);
  }, []);

  const pushData1 = PushDataLayer(
    "clickClasses",
    "more close service-visual pc",
    "More"
  );
  return (
    <>
      <section
        className=" bg-gradient-to-b from-gray-900 to-gray-800 text-white flex flex-col justify-center text-center"
        ref={element}
      >
        <div className="max-w-screen-lg mx-auto py-40">
          <h1 className=" text-3xl font-bold leading-normal">{data.h1}</h1>

          <Link to={data.url}>
            <div
              className={` rounded-lg  border-2 text-lg text-shadow-md font-bold mt-10 w-52 p-3 px-4 flex justify-center cursor-pointer shadow-lg mx-auto ${
                Trigger
                  ? "bg-orange-500 text-white border-transparent"
                  : " bg-transparent text-orange-500 border-orange-500"
              } transition duration-300`}
              onClick={() => pushData1()}
            >
              서비스 자세히 보기
            </div>
          </Link>
        </div>
      </section>
    </>
  );
};

export default App;
