import React from "react";
import DesktopView from "./DesktopView";
import MobileView from "./MobileView";
import { isMobile } from "react-device-detect";

const App = ({ data, children }: any) => (
  // h1,  height
  <>
    {data ? (
      <>
        {isMobile ? (
          <MobileView data={data} children={children} />
        ) : (
          <DesktopView data={data} children={children} />
        )}
      </>
    ) : (
      <div>데이터가 부족합니다</div>
    )}
  </>
);
export default App;
