import React from "react";
import DesktopView from "./DesktopView";
import MobileView from "./MobileView";
import { isMobile } from "react-device-detect";

const App = ({ data }: any) => (
  // h1, src, height
  <>
    {data ? (
      <>{isMobile ? <MobileView data={data} /> : <DesktopView data={data} />}</>
    ) : (
      <div>데이터가 부족합니다</div>
    )}
  </>
);
export default App;

