import styled from "styled-components";
import Slider, { Settings } from "react-slick";

const CustomCarousel = styled.div`
  width: 100%;
  & .slick-slider {
    height: 100%;
    & .slick-prev {
      z-index: 10;
      left: -0rem;
      padding-bottom: 1rem;
      &:before {
        font-size: 1.5rem;
        font-family: "Font Awesome 5 Free";
        font-weight: 900;
        color: grey;
        content: "\f053";
      }
    }
    & .slick-next {
      z-index: 10;
      right: 0.08rem;
      padding-bottom: 1rem;
      &:before {
        font-size: 1.5rem;
        font-family: "Font Awesome 5 Free";
        font-weight: 900;
        color: grey;
        content: "\f054";
      }
    }
    & .slick-dots {
      z-index: 10;
      bottom: -1.6rem;
      & button {
        &:before {
          color: black;
        }
      }
    }

    & div:focus {
      outline: none;
    }
  }
`;

const defualtChildren = [<div>1</div>, <div>2</div>, <div>3</div>];

const App = ({ children = defualtChildren }) => {
  const settings: Settings = {
    dots: true,
    infinite: true,
    fade: false,
    speed: 300,
    slidesToShow: 1,
    slidesToScroll: 1,
    lazyLoad: "ondemand",
  };
  return (
    <>
      <CustomCarousel>
        <Slider {...settings}>
          {children.map((val, idx) => (
            <>{val}</>
          ))}
        </Slider>
      </CustomCarousel>
    </>
  );
};

export default App;
