import React, { useReducer, useState } from "react";
import { withRouter } from "react-router-dom";
import { Link } from "react-router-dom";
import { throttle } from "throttle-debounce";
import ChannelService from "/home/po-main1-front/src/components/ChannelService";

import { data1 } from "/home/po-main1-front/src/atomic/templates/Header1/MobileView/index";

function reducer(state: any, action: any) {
  switch (action.type) {
    case "MENU_RESET":
      const menuReset = { ...state };
      menuReset.selected = false;
      return menuReset;

    case "MENU_SECLECT":
      const menuSelected = { ...state };
      menuSelected.selected
        ? (menuSelected.selected = false)
        : (menuSelected.selected = true);
      return menuSelected;

    case "MAIN_RESET":
      const mainReset = { ...state };
      mainReset.content.map((mainVal: any) => (mainVal.selected = false));
      return mainReset;

    case "MAIN_SELECT":
      const mainSelect = { ...state };
      mainSelect.content.map((mainVal: any) => (mainVal.selected = false));
      mainSelect.content[action.con1Idx].selected = true;
      return mainSelect;

    default:
      return state;
  }
}

const App = withRouter(({ location: { pathname } }: any) => {
  let data = data1;
  let track = "";

  const pathSplited = pathname.split("/");

  // if (pathSplited[1] === "t2") {
  //   data = data2;
  //   track = "/t2";
  // }

  const [state, dispatch] = useReducer(reducer, data);

  return (
    <>
      <header className="flex justify-left items-center w-full bg-white">
        {/* 대분류 */}
        <ul className="w-full">
          {data.content.map((con1Val: any, con1Idx: any) => (
            <div className="border-b border-gray-200 my-1">
              <li
                className={`py-2 flex justify-left `}
                onClick={() => {
                  state.content[con1Idx].selected
                    ? dispatch({ type: "MAIN_RESET", con1Idx })
                    : dispatch({ type: "MAIN_SELECT", con1Idx });
                }}
              >
                {state.content[con1Idx].content[0] ? (
                  <>{con1Val.title}</>
                ) : state.content[con1Idx].urlName[1] ? (
                  <a
                    href={`${con1Val.urlName[1]}`}
                    target="_blank"
                    rel="noreferrer"
                    onClick={() => {
                      dispatch({ type: "MENU_RESET" });
                    }}
                  >
                    {con1Val.title}
                  </a>
                ) : (
                  <Link
                    to={`${track}/${con1Val.urlName}`}
                    onClick={() => {
                      dispatch({ type: "MENU_RESET" });
                    }}
                  >
                    {con1Val.title}
                  </Link>
                )}
                {!state.content[con1Idx].content[0] ? (
                  <>
                    {" "}
                    <i className="fas fa-chevron-right text-gray-500 pl-2  relative top-1 pt-px  text-xs"></i>
                  </>
                ) : state.content[con1Idx].selected ? (
                  <>
                    <i className="fas fa-chevron-up text-gray-500 pl-2  relative top-1 pt-px  text-xs"></i>
                  </>
                ) : (
                  <>
                    <i className="fas fa-chevron-down text-gray-500 pl-2  relative top-1 pt-px  text-xs"></i>
                  </>
                )}
              </li>
              <div
                className={`w-full bg-white  ${
                  state.content[con1Idx].selected &&
                  state.content[con1Idx].content[0]
                    ? "block"
                    : "hidden"
                } 
                       `}
              >
                {/* 중분류 */}
                <ul className=" shadow-inner bg-gray-100 w-full py-2">
                  {con1Val.content.map((con2Val: any, con2Idx: any) => (
                    <>
                      <li className="py-2 flex justify-left pl-4">
                        <Link
                          to={`${track}/${con1Val.urlName[0]}/${con2Val.urlName[0]}`}
                          onClick={() => {
                            dispatch({ type: "MENU_RESET" });
                          }}
                        >
                          {con2Val.title}
                        </Link>
                      </li>
                      <div
                        className={`absolute left-0 top-12 w-full bg-white
                       `}
                      >
                        <ul></ul>
                      </div>
                    </>
                  ))}
                </ul>
              </div>
            </div>
          ))}
        </ul>
      </header>
    </>
  );
});

export default App;
