import { useReactiveVar } from "@apollo/client";
import { useHistory } from "react-router";
import { visualPaymentDataVar } from "./Var_priceData";

export default function App() {
  const visualPaymentData = useReactiveVar(visualPaymentDataVar);
  const onClick = () => {
    window.localStorage.setItem(
      "serviceVisualClickedData",
      JSON.stringify(visualPaymentData.map((val) => val.clicked).indexOf(true))
    );
  };
  const history = useHistory();
  return (
    <div
      className="w-max"
      onClick={async () => {
        onClick();
        history.push("/orderSheet");
      }}
    >
      <div className="flex  text-shadow-md bg-gradient-to-r from-orange-500 to-yellow-500 px-7 py-3 rounded-full cursor-pointer text-white w-max items-center">
        <img
          src={
            require("/home/po-main1-front/src/assets/서비스_비주얼2/아이콘/shuttle 2.png")
              .default
          }
          alt="바로 진행하기"
          className="mr-3 h-5/6"
        />
        바로 진행하기
      </div>
    </div>
  );
}
