import { useState } from "react";
import { isMobile } from "react-device-detect";
import Modal from "react-modal";
import Hook_scrollLock from "../Payment1_1/Hook_scrollLock";

const data1 = {
  button: <span>포케팅 서비스 이용약관 동의</span>,
  modal: <div>내용물</div>,
};

const customStyles = {
  content: {
    top: "51%",
    left: "50%",
    right: "auto",
    bottom: "auto",
    marginRight: "-50%",
    transform: "translate(-50%, -50%)",
    padding: 0,
    borderRadius: "0.5rem",
  },
};
Modal.setAppElement("#root");

export default function App({ data = data1 }) {
  const [modalIsOpen, setIsOpen] = useState(false);

  function openModal() {
    setIsOpen(true);
  }

  function afterOpenModal() {}

  function closeModal() {
    setIsOpen(false);
  }
  return isMobile ? (
    <div className="relative z-50 ">
      <span onClick={openModal}>{data.button}</span>
      <Modal
        isOpen={modalIsOpen}
        onAfterOpen={afterOpenModal}
        onRequestClose={closeModal}
        style={customStyles}
        contentLabel="Modal2"
      >
        <div className="flex justify-end mr-4 mt-2 ">
          <div onClick={closeModal} className=" cursor-pointer">
            <i className="far fa-times-circle text-xl text-gray-400"></i>
          </div>
        </div>
        <div
          className="overflow-y-scroll "
          style={{ height: "60vh", width: "90vw" }}
        >
          <div>
            <Hook_scrollLock />
            {data.modal}
          </div>
        </div>
      </Modal>
    </div>
  ) : (
    <div className="relative z-50 ">
      <span onClick={openModal}>{data.button}</span>
      <Modal
        isOpen={modalIsOpen}
        onAfterOpen={afterOpenModal}
        onRequestClose={closeModal}
        style={customStyles}
        contentLabel="Modal2"
      >
        <div className="flex justify-end mr-4 mt-2">
          <div onClick={closeModal} className=" cursor-pointer">
            <i className="far fa-times-circle text-xl text-gray-400"></i>
          </div>
        </div>
        <div
          className="overflow-y-scroll "
          style={{ height: "40rem", width: "30rem" }}
        >
          <div className="p-4">
            <Hook_scrollLock />
            {data.modal}
          </div>
        </div>
      </Modal>
    </div>
  );
}
