import { gql, useMutation, useReactiveVar } from "@apollo/client";
import { isMobile } from "react-device-detect";
import { useHistory } from "react-router";
import {
  consultingInpuValidate,
  Var_agreeCheck,
  Var_consultingInpuSetting,
  Var_consultingInputData,
} from "./Lib_consultingForm";

export default function App({ clickable = true }) {
  const agreeCheckbox2 = useReactiveVar(Var_agreeCheck);
  // 메일발송
  const consultingInputData = useReactiveVar(Var_consultingInputData);

  const prevUrl = JSON.parse(
    window.localStorage.getItem("prevUrl") || JSON.stringify("")
  );
  const curUrl = JSON.parse(
    window.localStorage.getItem("curUrl") || JSON.stringify("")
  );
  const nowUrl = window.location.pathname;

  // 액션팀에게
  const SENDMAIL_TO_ACTIONTEAM = gql`
    mutation {
      sendQuestionMailToAction(
        from: "poketing_mail_server@poketing.com"
        to: "sp_team@poketing.com"
        subject: "[공홈] 신규 컨설팅 문의 전달"
        brandName:"${consultingInputData[0]}"
        name:"${consultingInputData[1]}"
        phoneNumber:"${consultingInputData[2]}"
        email:"${consultingInputData[3]}"
        budget:"${consultingInputData[4]}"
        link:"${consultingInputData[5]}"
        etc:"${
          consultingInputData[6] +
          "<br><br>대행사: " +
          agreeCheckbox2 +
          (prevUrl === ""
            ? "<br><br>전전 페이지: 로그 확인 불가"
            : "<br><br>전전 페이지: https://poketing.com") +
          prevUrl +
          (curUrl === ""
            ? "<br><br>전 페이지: 로그 확인 불가"
            : "<br><br>전 페이지: https://poketing.com") +
          curUrl +
          "<br><br>폼 발송 페이지: https://poketing.com" +
          nowUrl
        }" 
      )
    }
  `;
  const [sendMailMutation1] = useMutation(SENDMAIL_TO_ACTIONTEAM);

  // 클라이언트에게
  const SENDMAIL_TO_CLIENT = gql`
    mutation {
      sendQuestionMailToClient(
        from: "sp_team@poketing.com"
        to: "${consultingInputData[3]}"
        subject: "[포케팅] 컨설팅 접수 확인/상담 안내 확인 부탁 드립니다."
        brandName:"${consultingInputData[0]}"
        name:"${consultingInputData[1]}"
        phoneNumber:"${consultingInputData[2]}"
        email:"${consultingInputData[3]}"
        budget:"${consultingInputData[4]}"
        link:"${consultingInputData[5]}"
        etc:"${consultingInputData[6]}"

      )
    }
  `;
  const [sendMailMutation2] = useMutation(SENDMAIL_TO_CLIENT);

  // 폴라베어에게
  const SENDMAIL_TO_CLIENT_POLARBEAR = gql`
    mutation {
      sendQuestionMailToClient(
        from: "sp_team@poketing.com"
        to: "polarbear@play--office.com"
        subject: "[포케팅] 컨설팅 접수 확인/상담 안내 확인 부탁 드립니다."
        brandName:"${consultingInputData[0]}"
        name:"${consultingInputData[1]}"
        phoneNumber:"${consultingInputData[2]}"
        email:"${consultingInputData[3]}"
        budget:"${consultingInputData[4]}"
        link:"${consultingInputData[5]}"
        etc:"${consultingInputData[6]}"

      )
    }
  `;
  const [sendMailMutation3] = useMutation(SENDMAIL_TO_CLIENT_POLARBEAR);

  // 메이에게
  const SENDMAIL_TO_CLIENT_MEI = gql`
    mutation {
      sendQuestionMailToClient(
        from: "sp_team@poketing.com"
        to: "mei@play--office.com"
        subject: "[포케팅] 컨설팅 접수 확인/상담 안내 확인 부탁 드립니다."
        brandName:"${consultingInputData[0]}"
        name:"${consultingInputData[1]}"
        phoneNumber:"${consultingInputData[2]}"
        email:"${consultingInputData[3]}"
        budget:"${consultingInputData[4]}"
        link:"${consultingInputData[5]}"
        etc:"${consultingInputData[6]}"

      )
    }
  `;
  const [sendMailMutation4] = useMutation(SENDMAIL_TO_CLIENT_MEI);

  const history = useHistory();
  // 폼체크하고 발송
  const formCheck = () => {
    consultingInpuValidate.map((val, idx) => {
      if (!val(consultingInputData[idx])) {
        const error = Var_consultingInpuSetting()[idx].validateError;
        throw error;
      }
    });
    sendMailMutation1();
    sendMailMutation2();
    sendMailMutation3();
    sendMailMutation4();
    window.localStorage.setItem(
      "consultingInputData",
      JSON.stringify(consultingInputData)
    );

    history.push("/service/visual-influencer/consulting/2");
  };

  return clickable ? (
    <div
      className="w-max"
      onClick={() => {
        try {
          formCheck();
        } catch (e) {
          alert(e);
        }
      }}
    >
      <div className="flex  text-shadow-md bg-gradient-to-br from-orange-500 to-orange-400 px-10 py-3 rounded-full cursor-pointer text-white w-max text-lg">
        전문가 컨설팅 신청
      </div>
    </div>
  ) : (
    <div className="w-max">
      <div className="flex  text-shadow-md bg-gradient-to-br  bg-gray-300 px-10 py-3 rounded-full text-white w-max text-lg">
        전문가 컨설팅 신청
      </div>
    </div>
  );
}
