import { useReactiveVar } from "@apollo/client";
import { useState } from "react";
import { isMobile } from "react-device-detect";
import { Atom_Input1 } from "./Atom_Input1";
import Con_loadinfoFormData from "./Hook_loadingCookieData";
import {
  apllicantDataValidate,
  formVarOnchange,
  Var_applicantData,
  Var_applicantInputSetting,
} from "./Lib_applicantFormData";

const data1 = {
  title: (
    <>
      캠페인 진행에 필요한 {isMobile ? <br /> : <></>}
      필수정보를 입력해주세요.
    </>
  ),
  subtitle: (
    <>
      * 꼭 입력해 주셔야 결제 이후 {isMobile ? <br /> : <></>}
      연락 드릴 수 있습니다.
    </>
  ),
  emailText: (
    <>
      이메일로 캠페인 진행을 안내 해드립니다. <br />
      수신 가능한 이메일 주소를 입력해 주세요.
    </>
  ),
};

export default function App() {
  const inputData = useReactiveVar(Var_applicantData);
  const layout = isMobile ? "w-11/12" : " w-full";
  return (
    <section className="">
      <div className={`${layout} mx-auto font-bold text-xl mb-2`}>
        {data1.title}
      </div>
      <div className={`${layout} mx-auto underline text-gray-500 mb-8`}>
        {data1.subtitle}
      </div>
      <ul className={`${layout} mx-auto mb-1`}>
        {Var_applicantInputSetting().map((val, idx) => (
          <li className="mb-4 last:mb-2">
            <div className="text-lg mb-1">{val.title}</div>
            <div className="h-0 relative z-30 flex justify-end">
              <div className=" h-10 w-max flex items-center mr-2">
                {apllicantDataValidate[idx](
                  inputData[idx].state ? inputData[idx].state : ""
                ) ? (
                  <>
                    <i className="fas fa-check-circle text-lg"></i>
                  </>
                ) : (
                  <></>
                )}
              </div>
            </div>
            <Atom_Input1
              {...val.inputProps}
              value={inputData[idx].state}
              onChange={(e) => {
                formVarOnchange(e, inputData, idx);
              }}
            />
          </li>
        ))}
      </ul>
      <div className={`text-gray-500 ${layout} mx-auto`}>{data1.emailText}</div>
    </section>
  );
}
