import React from "react";
import DesktopView from "./DesktopView";
import MobileView from "./MobileView";
import { isMobile } from "react-device-detect";
import { Helmet } from "react-helmet";
import LogPrevUrl from "../../../3organizations/LogPrevUrl";

const App = ({ data, children }: any) => (
  //
  <>
    <Helmet>
      <title>포케팅 - 고객사례</title>
      <meta
        name="description"
        property="og:description"
        content="포케팅 고객사례"
      />
    </Helmet>
    <LogPrevUrl />
    {true ? (
      <>
        {isMobile ? (
          <MobileView data={data} children={children} />
        ) : (
          <DesktopView data={data} children={children} />
        )}
      </>
    ) : (
      <div>데이터가 부족합니다</div>
    )}
  </>
);
export default App;
