import React, { useEffect } from "react";
import { withRouter } from "react-router-dom";
import { gql, useMutation } from "@apollo/client";

const App = withRouter(({ data, children, location, history }: any) => {
  // url search 정보에서 데이터 파싱
  const searchArray = location.search.replace("?", "").split("&");
  const paymentData = {
    orderId: searchArray[0].split("=")[1],
    paymentKey: searchArray[1].split("=")[1],
    amount: Number(searchArray[2].split("=")[1]),
  };

  // 로컬스토리지에서 데이터 파싱
  const localPaymentData = JSON.parse(
    window.localStorage.getItem("paymentData") ||
      JSON.stringify({ orderId: "", amount: 0 })
  );

  // 결제승인정보 전송
  const PAYMENT_APPROVAL = gql`
    mutation {
      approvePayment(
        orderId:"${paymentData.orderId}"
        paymentKey:"${paymentData.paymentKey}"
        amount:${paymentData.amount}
      ){
        ok
        error
      }
    }
  `;
  const [approvePaymentMutation] = useMutation(PAYMENT_APPROVAL);

  useEffect(() => {
    const approvePayment = async () => {
      try {
        if (
          paymentData.orderId === localPaymentData.orderId &&
          paymentData.amount === localPaymentData.amount
        ) {
          await approvePaymentMutation();
          alert(
            "진행 결정해 주셔서 감사드립니다.\n좋은 결정이 될 수 있도록\n올바른 전략과 방식으로\n진행해 드리겠습니다.\n\n결제 완료일을 기준으로 1 영업일 이내로\n전담 매니저가 연락 드립니다.\n조금만 기다려 주세요 "
          );
          history.push("/price-plan/custom-quote");
        } else {
          throw "결제오류";
        }
      } catch {
        alert(
          "결제가 실패하였습니다.\n다시 한 번 시도하면\n대부분 정상적으로 진행됩니다.\n다시 한 번 시도해 주세요!\n\n혹시 다시 시도 시에 실패한다면\n우측 하단의 채팅 문의 주시면\n신속하게 도와 드리겠습니다. "
        );
        history.push("/price-plan/custom-quote");
      }
    };
    approvePayment();
  }, []);

  return (
    <>
      <div></div>
    </>
  );
});

export default App;
