import React from "react";
import { PushDataLayer } from "../../../../hooks/PushDataLayer";
import channelIoShow from "../../../atoms/channelIoShow";

const App = ({ data }: any) => {
  const pushData1 = PushDataLayer(
    data.btn && data.btn[0].pushData.name,
    data.btn && data.btn[0].pushData.data,
    data.btn && data.btn[0].pushData.eventName
  );
  return (
    <section className="mb-10 ">
      <div className="h-0 relative z-0">
        <div
          className=""
          style={{
            height: data.height,
            backgroundImage: `url(${data.src})`,
            backgroundSize: `cover`,
            backgroundPosition: `center`,
          }}
        ></div>
      </div>
      <div
        className=" w-11/12 mx-auto relative z-10"
        style={{ height: data.height }}
      >
        <div className="text-white text-3xl pt-12 font-bold leading-snug text-shadow-md">
          {data.h1.map((h1Val: any) => (
            <>{h1Val}</>
          ))}

          {data.btn &&
            data.btn.map((btnVal: any) => (
              <>
                {" "}
                <div
                  className=" bg-orange-500 rounded-lg text-white text-lg font-bold text-shadow-md w-44 mt-12 p-3 px-4 flex justify-center cursor-pointer shadow-lg"
                  onClick={() => {
                    channelIoShow();
                    pushData1();
                  }}
                >
                  {btnVal.text}
                </div>
              </>
            ))}
        </div>
      </div>
    </section>
  );
};

export default App;
