import React, { useEffect } from "react";
import styled from "styled-components";
import Portal from "/home/po-main1-front/src/hooks/useFortal";
import { isMobile } from "react-device-detect";

const ModalOverlay: any = styled.div`
  box-sizing: border-box;
  display: ${(props: any) => (props.visible ? "block" : "none")};
  position: fixed;
  top: 0px;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.6);
  z-index: 999;
`;

const ModalWrapper: any = styled.div`
  box-sizing: border-box;
  display: ${(props: any) => (props.visible ? "block" : "none")};
  position: fixed;
  top: 0px;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  overflow: auto;
  outline: 0;
  transition: 1s;
`;
const ModalInner: any = styled.div`
  box-sizing: border-box;
  position: relative;
  box-shadow: 0 0 6px 0 rgba(0, 0, 0, 0.5);
  background-color: white;
  border-radius: 10px;
  width: ${(props: any) => props.width};
  max-width: 80rem;
  top: 50%;
  transform: translateY(-50%);
  margin: 0 auto;
  padding: 0 1rem;
  padding-top: 20px;
  padding-bottom: 40px;
  &:focus {
    outline: none;
  }
  & div {
    max-height: ${isMobile ? "80vh" : "80vh"};
    overflow-y: scroll;
    width: 100%;
  }
`;

const App = ({
  onClose,
  maskClosable,
  closable,
  visible,
  width,
  children,
}: any) => {
  const onMaskClick = (e: any) => {
    if (e.target === e.currentTarget) {
      onClose(e);
    }
  };
  const close = (e: any) => {
    if (onClose) {
      onClose(e);
    }
  };
  useEffect(() => {
    document.body.style.cssText = `position: fixed; top: -${window.scrollY}px; width:100%;`;
    return () => {
      const scrollY = document.body.style.top;
      document.body.style.cssText = `position: ""; top: "";`;
      window.scrollTo(0, parseInt(scrollY || "0") * -1);
    };
  }, []);

  return (
    <Portal elementId="modal-root">
      <ModalOverlay visible={visible} />
      <ModalWrapper
        className={``}
        onClick={maskClosable ? onMaskClick : null}
        tabIndex={-1}
        visible={visible}
      >
        <ModalInner className={``} tabIndex={0} visible={visible} width={width}>
          {closable && (
            <span className="modal-close flex justify-end mb-3">
              <i
                className="fas fa-times fa-lg  cursor-pointer"
                onClick={close}
              ></i>
            </span>
          )}
          <div className="contents">{children}</div>
        </ModalInner>
      </ModalWrapper>
    </Portal>
  );
};
export default App;

