import { useState } from "react";
import TagManager from "react-gtm-module";
import { useHistory } from "react-router-dom";

const data1 = {};

export const useDataTag = (data = data1) => {
  const [isSent, setIsSent] = useState(false);
  const history = useHistory();
  const checkToggle = () => {
    setIsSent(true);
    history.listen(() => {
      setIsSent(false);
    });
  };

  const onClick = () => {
    checkToggle();
    const gtmArgs = {
      dataLayer: data,
    };
    TagManager.dataLayer(isSent ? {} : gtmArgs);
  };
  return onClick;
};
