import { useState } from "react";
import TagManager from "react-gtm-module";
import { useHistory } from "react-router-dom";

export const PushDataLayer = (title, arg, event?) => {
  const [isSent, setIsSent] = useState(false);
  const history = useHistory();
  const checkToggle = () => {
    setIsSent(true);
    history.listen(() => {
      setIsSent(false);
    });
  };
  const onClick = () => {
    checkToggle();
    const tagManagerArgs = event
      ? {
          dataLayer: {
            event,
            eventModel: {
              [title]: arg,
            },
          },
        }
      : {
          dataLayer: {
            [title]: arg,
          },
        };
    TagManager.dataLayer(isSent ? {} : tagManagerArgs);
  };
  return onClick;
};
