                      <Input_bigConsulting
                        value={inputData[idx]}
                        onChange={(e) => {
                          formVarOnchange(e, inputData, idx);
                        }}
                        placeholder={val.inputProps?.placehoder}
                        name={val.inputProps?.name}
                      />
