export const consultingInpuValidate = [
  (val) => {
    return val.length > 0;
  },
  (val) => {
    return val.length > 1;
  },
  (val) => {
    return val.length > 10;
  },
  (val) => {
    return val.match(
      /^[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*@[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*.[a-zA-Z]{2,3}$/i
    );
  },
  // 예산
  (val) => {
    return val !== "선택";
  },
  (val) => {
    return val.length > 0;
  },
  (val) => {
    return true;
  },
];

export function formVarOnchange(e, data, id) {
  const newData = data.map((val, idx) =>
    idx === id ? (val = e.target.value) : val
  );
  Var_consultingInputData(newData);
}
